import React from "react";
import "./title.css";

const Title = ({children}) => {
	const words = children.split(" ");

	return(
		<h1 className="title">
			{
				words.map((word, index) => {
					return(
						<div className="title__word" key={index}>
							<div className="title__word-content">{word}&nbsp;</div>
						</div>
					);
				})
			}
		</h1>
	)
}

export default Title;
