import React from "react";
import "./text.css";

const Text = ({children}) => {
	const words = children.split(" ");
	console.log(words);

	return(
		<p className="text">
			{
				words.map((word, index) => {
					return(
						<div className="text__word">
							<div className="text__word-content" key={index}>{word}&nbsp;</div>
						</div>
					);
				})
			}
		</p>
	);
}

export default Text;
