import React, {useRef} from 'react';
import Block from "./components/block";
import Image from "./components/image";
import Text from "./components/text";
import Title from "./components/title";
import store from "./store";
import "./App.css";

function Content() {
	return(
		<main>
			<div className="background">
				{
					[...Array(4)].map((_, index) => (
						<div className="background__tile"></div>
					))
				}
			</div>

			<section className="section">
				<Block>
					<Title>
						Somewhere over the rainbow
					</Title>
				</Block>

				<Block factor={0.3}>
					<div className="section__cover-img">
						<Image imgSrc="/img1.jpeg" />
					</div>
				</Block>

				<Block>
					<Text>
						Amanda is a Brazilian fashion designer based in Miami. From an early age she was interested in the arts and manual tasks and her favorite activity was always drawing, which she loves to do until the present days.  
					</Text>
				</Block>
			</section>
		</main>
	);
}

function App() {
	const scrollRef = useRef();
	const onScroll = () => (store.top = scrollRef.current.scrollTop);

	return(
		<div id="scroll-area" ref={scrollRef} onScroll={onScroll}>
			<div id="content-height"></div>
			<div id="content">
				<Content />
			</div>	
		</div>
	);
}

export default App;
