import React, {createRef, useEffect} from "react";
import store from "./../store"; 
import "./block.css";

const Block = ({factor=1.5, direction=-1, children}) => {
	const blockRef = createRef();

	const lerp = (a, b, n) => (
		(1-n) * a + n * b
	)

	function init() {
		translate();
		const rect = blockRef.current.getBoundingClientRect(); 
		
		if(rect.top <= window.innerHeight) {
			return
		}

		const distanceFromViewport = rect.top - window.innerHeight;
		const blockOffset = factor*distanceFromViewport - window.innerHeight; 

		blockRef.current.style.transform = `translateY(${blockOffset}px)`;
		console.log(blockRef.current.style.transform);
	}

	function translate() {
		const block = blockRef.current;
		const regex = /\.*translateY\((.*)px\)/i;
		const currentTranslate = block.style.transform === "" ? 0 : regex.exec(block.style.transform)[1];
		const currentTop = store.top;
		const newTop = lerp(currentTranslate, currentTop*factor*direction, 0.1);

		block.style.transform = `translateY(${newTop}px)`;
		requestAnimationFrame(translate);
	}

	// eslint-disable-next-line
	useEffect(() => init(), []);

	return(
		<div ref={blockRef}>
			{children}
		</div>
	);
}

export default Block;
